.boxMiddleContact {
    display: flex;
    /**/
    flex-direction: column;
    align-items: center;
    /**/
    width: 100%;

}

.contactPhone {
    display: flex;
    width: 100%;
    padding: 10px;
    justify-content: center;
    flex-direction: column;
}

.btnBox {
    display: flex;
    justify-content: right;
    margin-bottom: 15px;
}

@media (min-width: 768px) {
    .contactPhone {
        width: 90%;
    }

    .footerBtnFont {
        border-radius: 12px;
        border: hsl(57, 48%, 51%) solid 4px;
        /*font-size: calc(11px + 6 * ((100vw - 320px) / 1158));*/
        /**/
        font-family: "Roboto", sans-serif;
        font-weight: 300;
        font-size: calc(20px + 5 * ((100vw - 320px) / 1158));
    }

    .footerBtnFont:hover {
        border: #9c9287 solid 4px;
        /*font-size: calc(11px + 6 * ((100vw - 320px) / 1158));*/

        /**/
        font-family: "Roboto", sans-serif;
        font-weight: 300;
        font-size: calc(20px + 5 * ((100vw - 320px) / 1158));
    }

    .footerBtnFont:focus {
        border: hsl(57, 48%, 51%) solid 4px;
    }
}
